import {eId, eClass, darkLightMode} from '../../components/helpers/StylingHelpers';

function PlayerAccessStyle(){
    setDarkLightMode();
    setStyle();

    var userAgent = navigator.userAgent || navigator.vendor || window.opera;
    if (!(/android/i.test(userAgent))) {
        window.addEventListener('resize', setStyle);
    }
}

export default PlayerAccessStyle;

function setStyle(){
    eId("playerAccess").style.minHeight = eId('playerAccess').offsetWidth * 0.6 + 'px';

    if(window.innerWidth > window.innerHeight){
        eId('playerAccess').style.borderRadius = parseInt(eId('playerAccess').offsetHeight) * 0.07 + 'px';
    } else {
        eId('playerAccess').style.borderRadius = parseInt(eId('playerAccess').offsetWidth) * 0.07 + 'px';
    }

    if(eId('playerAccess').offsetWidth > eId('playerAccess').offsetHeight){
        eId("accessForm").className = eId("accessForm").className.replace(" mb-5", "") + " mb-5";
    } else {
        eId("accessForm").className = eId("accessForm").className.replace(" mb-2","") + " mb-2";
    }

    eId("accessForm").className = eId("accessForm").className.replace(" card-body","") + " card-body";

    setInput('input');
    setBtn('btn');
}

function setDarkLightMode(){
    darkLightMode("playerAccess");
    darkLightMode('input');
    darkLightMode('button');
}

function setInput(ref){
    let input = eClass(ref);
    for(let i = 0; i < input.length; i++){
        input[i].style.borderRadius = input[0].offsetWidth * 0.025 + 'px';
    }
}

function setBtn(ref){
    let btn = eClass(ref);
    for(let i = 0; i < btn.length; i++){
        btn[i].style.borderRadius = btn[0].offsetWidth * 0.15 + 'px';
    }
}